    height: 200px;
    width: 200px;
    border-radius: 100%;
    object-fit: cover;
  } */

/* Scroll down */

.scroll__down { 
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/* MEDIA QUERIES (MEDIUM DEVICES) */
@media screen and (max-width: 1024px) {
    header { 
        height: 68vh;
    }
}

/* MEDIA QUERIES (SMALL DEVICES) */
@media screen and (max-width: 600px) {
    header{
        height: 100vh;
    }

    .header__socials, .scroll__down { 
        display: none;
    }
}

