.about__container { 
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
}

.about__me { 
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg, transparent, var(--color-primary), transparent);
    display: grid;
    place-items: center;
}

.about__me__img { 
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(6deg);
    aspect-ratio: 1/1;
    transition: var(--transition);
}

.about__me__img:hover{
    transform: rotate(0deg);
}

.about__cards { 
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
}

.about__card { 
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 2rem;
    padding: 2rem;
    text-align: center;
}

.about__card:hover { 
    background: transparent;
    border-color: var(--color-primary);
    transition: var(--transition);
    cursor: default;
}

.about__icon { 
    font-size: 1.5rem;
    color: var(--color-primary);
    margin-bottom: 1rem;
}

.about__card h5 {
    font-size: 0.95rem;
}

.about__card small { 
    font-size: 0.7rem;
    color: var(--color-light);
}

.about__content p { 
    margin: 2rem 2.6rem;
    font-size: 0.8rem;
    color: var(--color-light);
}


/* MEDIA QUERIES (MEDIUM DEVICES) */
@media screen and (max-width: 1024px) {
    .about__container { 
        grid-template-columns: 1fr;
        gap: 0rem;
    }

    .about__me{ 
        width: 50%;
        margin: 2rem auto 4rem;
    }

    .about__content p {
        margin: 1rem 0 1.5rem;
    }
}

/* MEDIA QUERIES (SMALL DEVICES) */
@media screen and (max-width: 600px) {
  .about__me { 
    width: 65%;
    margin: 0 auto 3rem;
  }

  .about__cards { 
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
  }

  .about__content { 
    text-align: center;
  }

  .about__content p { 
    margin: 1.5rem 0;
  }
}